import Vue from 'vue';
import 'src/components/style/font/iconfont.js';
import {
  Table as ElTable,
  TableColumn as ElTableColumn,
  Tag as ElTag,
  Tabs as ElTabs,
  TabPane as ElTabPane,
  RadioGroup as ElRadioGroup,
  RadioButton as ElRadioButton,
  Radio as ElRadio,
  Slider as ElSlider,
  InputNumber as ElInputNumber,
  Select as ElSelect,
  Option as ElOption
} from 'element-ui';
import {
  Breadcrumb,
  Row,
  Col,
  Layout,
  Header,
  Icon,
  Button,
  Popover,
  Form,
  Upload,
  Input,
  Search,
  Select,
  Radio,
  Checkbox,
  DatePicker,
  Loading,
  Message,
  CascaderSelect,
  Switch,
  AutoComplete,
  Tag,
  Steps,
  Modal,
  Dropdown,
  Pagination,
  CardList,
  Card,
  Tabs,
  TabPane,
  Scrollbar,
  Tree,
  Alert,
  Progress,
  Slider,
  Menu,
  Table,
  TableTags,
  Tooltip
} from '@gs-ui/gs-ui';
import CollapseTransition from '@gs-ui/gs-ui/lib/_utils/transition';
import VueCodemirror from 'vue-codemirror';
import loadjs from 'loadjs';
import Fullscreen from 'vue-fullscreen';

// 引入eachrts 按需加载
import ECharts from 'vue-echarts/components/ECharts';
import 'echarts/lib/chart/funnel';
import 'echarts/lib/chart/gauge';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/graph';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/chart/scatter';
import 'echarts/lib/component/singleAxis';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/legendScroll';
import 'echarts/lib/component/graphic';
import 'echarts/lib/component/title';
import 'echarts/lib/component/toolbox';
import 'echarts/lib/chart/pie';

// language
import 'codemirror/mode/sql/sql.js';
// theme css
import 'codemirror/lib/codemirror.css';

// 引入chart组件
Vue.component('chart', ECharts);

// element-ui
Vue.use(ElTable);
Vue.use(ElTableColumn);
Vue.use(ElTag);
Vue.use(ElTabs);
Vue.use(ElTabPane);
Vue.use(ElRadioGroup);
Vue.use(ElRadioButton);
Vue.use(ElSlider);
Vue.use(ElRadio);
Vue.use(ElInputNumber);
Vue.use(ElSelect);
Vue.use(ElOption);

Vue.use(CollapseTransition);
Vue.use(Breadcrumb);
Vue.use(Row);
Vue.use(Col);
Vue.use(Layout);
Vue.use(Header);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Popover);
Vue.use(Form);
Vue.use(Upload);
Vue.use(Input);
Vue.use(Search);
Vue.use(Select);
Vue.use(Radio);
Vue.use(Checkbox);
Vue.use(DatePicker);
Vue.use(Loading);
Vue.use(Message);
Vue.use(CascaderSelect);
Vue.use(Switch);
Vue.use(AutoComplete);
Vue.use(Tag);
Vue.use(Steps);
Vue.use(Modal);
Vue.use(Dropdown);
Vue.use(Pagination);
Vue.use(CardList);
Vue.use(Card);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Scrollbar);
Vue.use(Tree);
Vue.use(Alert);
Vue.use(Progress);
Vue.use(Slider);
Vue.use(Menu);
Vue.use(Table);
Vue.use(TableTags);
Vue.use(Tooltip);
Vue.use(VueCodemirror);

Vue.use(Fullscreen);

loadjs([`/config.js?_=${new Date().getTime()}`], 'config');
loadjs.ready('config', () => {
  document.title = window.systemConfigs.TITLE;

  let link = document.querySelector("link[rel*='icon']") || document.createElement('link');
  link.type = 'image/x-icon';
  link.rel = 'shortcut icon';
  link.href = window.systemConfigs.FAVICON;
  document.getElementsByTagName('head')[0].appendChild(link);

  import('./setup.js');
});



